/* Cubic Bezier Transition */
/***
Error Pages
***/
.page-404 {
    text-align: center; }

.page-404 .number {
    position: relative;
    top: 35px;
    display: inline-block;
    letter-spacing: -10px;
    margin-top: 0px;
    margin-bottom: 10px;
    line-height: 128px;
    font-size: 128px;
    font-weight: 300;
    color: #7bbbd6;
    text-align: left; }

.page-404 .details {
    margin-right: 40px;
    display: inline-block;
    padding-top: 0px;
    text-align: right; }
.page-500 {
    text-align: center; }

.page-500 .number {
    display: inline-block;
    letter-spacing: -10px;
    line-height: 128px;
    font-size: 128px;
    font-weight: 300;
    color: #ec8c8c;
    text-align: left; }

.page-500 .details {
    margin-right: 40px;
    display: inline-block;
    text-align: right; }
.page-404-full-page {
    overflow-x: hidden;
    padding: 20px;
    margin-bottom: 20px;
    background-color: #fafafa !important; }

.page-404-full-page .details input {
    background-color: #ffffff; }

.page-404-full-page .page-404 {
    margin-top: 100px; }
.page-500-full-page {
    overflow-x: hidden;
    padding: 20px;
    background-color: #fafafa !important; }

.page-500-full-page .details input {
    background-color: #ffffff; }

.page-500-full-page .page-500 {
    margin-top: 100px; }
.page-404-3 {
    background: #000 !important; }

.page-404-3 .page-inner img {
    left: 0;
    bottom: 0;
    z-index: -1;
    position: absolute; }

.page-404-3 .error-404 {
    color: #fff;
    text-align: right;
    padding: 70px 20px 0; }

.page-404-3 h1 {
    color: #fff;
    font-size: 130px;
    line-height: 160px; }

.page-404-3 h2 {
    color: #fff;
    font-size: 30px;
    margin-bottom: 30px; }

.page-404-3 p {
    color: #fff;
    font-size: 16px; }

@media (max-width: 480px) {
    .page-404 .number,
    .page-500 .number,
    .page-404 .details,
    .page-500 .details {
        text-align: center;
        margin-right: 0px; }
    .page-404-full-page .page-404 {
        margin-top: 30px; }
    .page-404-3 .error-404 {
        text-align: right;
        padding-top: 10px; }
    .page-404-3 .page-inner img {
        left: 0;
        bottom: 0;
        z-index: -1;
        position: fixed; } }